import ExternalLink from './ExternalLink';


interface ISocialsTabProps {
  href: string;
  icon: React.ReactNode;
};

const SocialsTab = (props: ISocialsTabProps) => {
  return (
    <ExternalLink className='transition-all duration-300 text-green-600 hover:text-green-400 hover:scale-125' href={props.href} >{props.icon}</ExternalLink>
  );
};

export default SocialsTab;
