import ExternalLink from './ExternalLink';
import EnchancedText from './EnchancedText';

interface IExperienceCardProps {
  position: string;
  company: string;
  companyUrl: string;
  startDate: string;
  endDate: string;
  children: React.ReactNode;
};

const ExperienceCard = (props: IExperienceCardProps) => {

  return (
    <div className='w-full bg-black bg-opacity-30 p-4'>
      <h1 className='font-semibold'>{props.position} @<EnchancedText><ExternalLink href={props.companyUrl}>{props.company}</ExternalLink></EnchancedText></h1>
      <h2 className='font-semibold text-sm opacity-50'>{props.startDate} - {props.endDate}</h2>
      <ul className='list-inside text-sm mt-2'>
        {props.children}
      </ul>
    </div>
  );
};

export default ExperienceCard;
