
interface IStickyBarProps {
  postion: 'top' | 'bottom' | 'left' | 'right';
  center?: boolean;
  className?: string;
  children?: React.ReactNode;
};

const StickyBar = (props: IStickyBarProps) => {

  let classes = 'fixed flex lg:bg-opacity-100 z-50';
  let fade = '';

  switch (props.postion) {
    case 'top':
      fade = 'down';
      classes = classes.concat(' top-0 w-full flex-row h-20');
      break;
    case 'bottom':
      fade = 'up';
      classes = classes.concat(' bottom-0 w-full flex-row h-20');
      break;
    case 'left':
      fade = 'right';
      classes = classes.concat(' left-0 h-full flex-col w-20');
      break;
    case 'right':
      fade = 'left';
      classes = classes.concat(' right-0 h-full flex-col w-20');
      break;
    default:
      fade = 'bottom';
      classes = classes.concat(' top-0 w-full flex-row h-20');
      break;
  }

  if (props.center === true || props.center === undefined) {
    classes = classes.concat(' justify-center items-center');
  }


  return (
    <div
      data-aos={`fade-${fade}`}
      data-aos-delay='1500'
      className={`${classes} ${props.className ?? ''}`}
    >
      {props.children}
    </div>
  );
};

export default StickyBar;
