import { useEffect, useState } from 'react';
import EnchancedText from './EnchancedText';
import { CornerDownLeft } from 'react-feather';

interface ITerminalPromptProps {
  prompt: string;
}

const TerminalPrompt = (props: ITerminalPromptProps) => {
  const prompt = props.prompt + ' ' ;
  const [currentText, setCurrentText] = useState<string>('');
  const [enterHit, setEnterHit] = useState<boolean>(false);

  useEffect(() => {
    if (currentText.length >= prompt.length) {
      // Hit enter
      const enterInterval = setInterval(() => {
        setEnterHit(true);
      }, 500);
      return () => clearInterval(enterInterval);
    };
    const interval = setInterval(() => {
      setCurrentText(currentText + prompt[currentText.length]);
    }, 100);
    return () => clearInterval(interval);
  }, [currentText, prompt]);

  return (
    <span className='w-max'>
      <EnchancedText className='font-bold opacity-20'>~$ </EnchancedText>
      <span>{currentText}</span>
      {
        enterHit && <>
          <EnchancedText className='font-bold'>
            <CornerDownLeft className='inline opacity-20' style={{height: '3.75rem', width: '2.4rem'}}/>
          </EnchancedText>
          <br/>
        </>
      }
      <span id='carrot' className='bg-green-400 bg-opacity-80 hidden lg:inline-block relative -bottom-1' style={{height: '3.75rem', width: '2.4rem'}} />
    </span>
  );
};

export default TerminalPrompt;
