interface ISectionProps {
  id: string;
  className?: string;
  children?: React.ReactNode;
}

const Section = (props: ISectionProps) => {
  return (
    <section id={props.id} className={`w-screen lg:px-80 relative flex ${props.className ?? ''}`} >
      {props.children}
    </section>
  );
};

export default Section;
