import TerminalTitle from './TerminalTitle';

interface ITiteledCoolBoxProps {
  title: string;
  children: React.ReactNode;
  fade: 'left' | 'right';
}

const TiteledCoolBox = (props: ITiteledCoolBoxProps) => {
  return (
    <div data-aos={`fade-${props.fade}`} data-aos-mirror='true' className='flex flex-col'>
        <TerminalTitle text={props.title} className={`text-center ${props.fade === 'right' ? 'lg:text-left' : 'lg:text-right'}`}/>
        <div className='cool-box flex items-center justify-center'>
          <div className='cool-box-content p-4 font-semibold flex flex-col space-y-4'>
            {props.children}
          </div>
        </div>
      </div>
  );
};

export default TiteledCoolBox;
