interface IExternalLinkProps {
  href: string;
  className?: string;
  children?: React.ReactNode;
}

const ExternalLink = (props: IExternalLinkProps) => {
  return (
    <a target='_blank' rel='noreferrer' href={props.href} className={`inline-flex items-center ${props.className ?? ''}`}>
      &nbsp;{props.children}
    </a>
  );
};

export default ExternalLink;
