
interface ISelectionTabProps {
  title: string;
  selected: boolean;
  onClick: () => void;
};

const SelectionTab = (props: ISelectionTabProps) => {

  const selectedClasses = 'px-4 flex items-center w-full uppercase text-sm text-green-400 border-b-8 lg:border-b-0 lg:border-r-8 border-green-400 bg-black bg-opacity-20';
  const unselectedClasses = 'px-4 flex items-center w-full uppercase text-sm transition-all duration-300 cursor-pointer hover:text-green-400 hover:border-b-8 lg:hover:border-b-0 lg:hover:border-r-8 border-green-400 hover:bg-black hover:bg-opacity-20';
  return (
    <div className={props.selected ? selectedClasses: unselectedClasses} onClick={props.onClick} style={{height: '48px'}}>{props.title}</div>
  );
}

export default SelectionTab;
