interface IButtonLinkProps {
  href: string;
  type?: 'primary' | 'secondary';
  className?: string;
  children?: React.ReactNode;
}

const ButtonLink = (props: IButtonLinkProps) => {

  const { type = 'primary' } = props;

  const typeClasses = type === 'primary' ?
    'bg-green-500 text-black border-2 border-green-500 text-white' :
    'bg-transparent text-green-500 border border-2 text-green-500 border-green-500 hover:text-white';

  return (
    <a className={`inline-flex cursor-pointer font-bold rounded-full transition-all ease-linear duration-300 py-2 px-4 hover:bg-green-400 hover:border-green-400 ${typeClasses} ${props.className ?? ''}`} href={props.href} target='_blank' rel='noreferrer'>
      {props.children}
    </a>
  );
};

export default ButtonLink;
