
interface INavMenuTabProps {
  section: string;
  icon: React.ReactNode;
  text: string;
}

const NavMenuTab = (props: INavMenuTabProps) => {

  const toSection = () => {
    const section = document.getElementById(props.section);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <div onClick={toSection} className='cursor-pointer flex flex-col place-items-center -rotate-90 transition-all duration-300 hover:rotate-0 text-green-600 hover:text-green-400'>
      {props.icon}
      <p>{props.text}</p>
    </div>
  );
};

export default NavMenuTab;
