import { GitHub, ExternalLink as ExternalLinkIcon } from 'react-feather';
import ExternalLink from './ExternalLink';
import EnchancedText from './EnchancedText';
import { TProject } from '../../types/project';

interface IProjectCardProps {
  project: TProject;
  orientation?: 'left' | 'right';
}

const ProjectCard = (props: IProjectCardProps) => {
  const rightOrientation = props.orientation === 'right';
  const project = props.project;
  return (
    <div data-aos={`fade-${rightOrientation ? 'left' : 'right'}`} className={`relative md:flex ${rightOrientation ? 'md:flex-row' : 'md:flex-row-reverse'}`}>
      <div className={`project-img absolute m-auto left-0 right-0 md:relative md:${rightOrientation ? 'left-20' : 'right-20'}`}>
        <img
          src={project.image}
          alt={project.title}
        />
      </div>
      <div className={`absolute flex flex-col flex-grow justify-start space-y-4 md:relative top-10 ${rightOrientation ? 'md:right-10 md:items-end' : 'md:left-10 md:items-start' }`}>
        <div className='flex flex-row w-full px-2 md:px-0'>
          <EnchancedText className='text-xl uppercase font-bold' ><h1>{project.title}</h1></EnchancedText>
          <div className="flex-grow" />
          <div className={`flex md:hidden flex-row space-x-4 ${rightOrientation ? 'md:justify-end' : 'md:justify-start'}`}>
            {project.demo && (
              <ExternalLink href={project.demo}><EnchancedText><ExternalLinkIcon /></EnchancedText></ExternalLink>
            )}
            {project.repo && (
              <ExternalLink href={project.repo}><EnchancedText><GitHub /></EnchancedText></ExternalLink>
            )}
          </div>
        </div>
        <div className='flex flex-row w-full px-2 md:px-0 items-baseline'>
          <h2 className={`pl-2 md:pl-0 text-green-400 md:text-inherit text-sm uppercase font-bold ${rightOrientation ? 'md:text-right' : 'md:text-left'}`}>{project.type}</h2>
          <div className="flex-grow" />
          <div className={`flex md:hidden flex-row`}>
            {project.technologies.map((tech, index) => (
              <div key={index} className='text-sm uppercase font-semibold px-2'>
                <EnchancedText>{tech}</EnchancedText>
              </div>
            ))}
          </div>
        </div>
        <p style={{maxWidth: '500px'}} className={`text-sm p-4 w-full bg-black font-bold ${rightOrientation ? 'md:text-right' : 'md:text-left'}`}>{project.description}</p>
        <div className={`hidden md:flex flex-row flex-wrap w-5/6 ${rightOrientation ? 'md:justify-end' : 'md:justify-start'}`}>
          {project.technologies.map((tech, index) => (
            <div key={index} className='text-sm uppercase font-bold p-2'>
              <EnchancedText>{tech}</EnchancedText>
            </div>
          ))}
        </div>
        <div className={`hidden md:flex flex-row space-x-4 ${rightOrientation ? 'md:justify-end' : 'md:justify-start'}`}>
          {project.demo && (
            <ExternalLink href={project.demo}><EnchancedText><ExternalLinkIcon /></EnchancedText></ExternalLink>
          )}
          {project.repo && (
            <ExternalLink href={project.repo}><EnchancedText><GitHub /></EnchancedText></ExternalLink>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProjectCard;
